<template>
  <div class="profile-modal">
    <Close class="story-modal__close"/>
    <Story :storyData="user">
      <template slot="footer">
        <span>
          <Icon name="Group"/>
          {{ activeItem && activeItem.viewingUsers ? activeItem.viewingUsers.length : 0 }} Kişi gördü
        </span>
        <Button class="-position-right" @click="deleteStory(activeItem.id)">
          <Icon name="Bin"/>
        </Button>
      </template>
    </Story>
  </div>
</template>

<script>
import { mapGetters, mapMutations } from "vuex";
export default {
  name: "Profile",
  data() {
    return {
      time: 0,
      stop: false,
      loading: true
    };
  },
  computed: {
    ...mapGetters({
      user: "user",
      activeStoryItemIndex: "activeStoryItemIndex"
    }),
    activeItem() {
      return this.user.stories[this.activeStoryItemIndex];
    }
  },
  methods: {
    ...mapMutations([
      "setActiveStoryItemIndex",
      "storyItemDelete",
      "toggleModal"
    ]),
    deleteStory(id) {
      if (this.user.stories.length === 1) {
        this.toggleModal(() => {
          this.storyItemDelete(id);
        });
      } else {
        this.storyItemDelete(id);
      }
    }
  }
};
</script>

<style lang="scss">
.profile-modal {
  width: 100%;
  height: 100%;
  color: #fff;

  &__footer {
    display: flex;
    align-items: center;
    color: #fff;
    padding: 15px;
  }
}
</style>