<template>
  <div class="story-modal" v-hammer:press="onPress" v-hammer:pressup="onPressup">
    <Close class="story-modal__close"/>
    <Cube3d :rotateY="rotateY" v-hammer:swipe.all="onSwipe">
      <template slot="back" v-if="activeStory && dimension === 0">
        <Story :storyData="activeStory" :pause="pause"/>
      </template>
      <template slot="left" v-if="prevStory && dimension === -1">
        <Story :storyData="prevStory" :pause="pause"/>
      </template>
      <template slot="front" v-if="activeStory && dimension === 0">
        <Story :storyData="activeStory" :pause="pause"/>
      </template>
      <template slot="right" v-if="nextStory && dimension === 1">
        <Story :storyData="nextStory" :pause="pause"/>
      </template>
    </Cube3d>
  </div>
</template>


<script>
import { mapMutations, mapGetters } from "vuex";
export default {
  name: "StoryModal",
  data() {
    return {
      rotateY: 0,
      direction: "right",
      dimension: 0,
      pause: false
    };
  },
  computed: {
    ...mapGetters({
      activeStory: "activeStory",
      prevStory: "prevStory",
      nextStory: "nextStory",
      activeStoryIndex: "activeStoryIndex",
      activeStoryItemIndex: "activeStoryItemIndex"
    })
  },
  watch: {
    dimension(dimension) {
      if (dimension < -1 || dimension > 1) {
        this.dimension = 0;
        if (dimension < -1) {
          this.setActiveStoryByIndex(this.activeStoryIndex - 2);
        } else {
          this.setActiveStoryByIndex(this.activeStoryIndex + 2);
        }
      }
    }
  },
  methods: {
    ...mapMutations([
      "toggleModal",
      "goNextStory",
      "goPrevStory",
      "setActiveStoryByIndex",
      "setActiveStoryItemIndex"
    ]),
    onPress() {
      this.pause = true;
    },
    onPressup() {
      this.pause = false;
    },
    onSwipe(e) {
      switch (e.type) {
        case "swipedown":
          this.direction = "down";
          this.swipeDown();
          break;
        case "swipeleft":
          this.direction = "left";
          this.swipeLeft(e);
          break;
        case "swiperight":
          this.direction = "right";
          this.swipeRight(e);
          break;
      }
    },
    swipeDown() {
      this.toggleModal();
    },
    swipeRight(e) {
      if (!this.prevStory) {
        this.toggleModal();
      }
      this.dimension -= 1;
      this.rotateY += 90;
    },
    swipeLeft(e) {
      if (!this.nextStory) {
        this.toggleModal();
      }
      this.dimension += 1;
      this.rotateY -= 90;
    }
  }
};
</script>

<style lang="scss">
.story-modal {
  height: 100%;

  &__close {
    position: absolute;
    top: 30px;
    right: 10px;
    z-index: 99;
  }
}
</style>
