<template>
  <div class="modal" v-if="modalVisible" tabindex="0" @keyup.esc="toggleModal">
    <transition appear enter-active-class="fadeIn" leave-active-class="fadeOut">
      <div class="modal__content" v-if="modalName">
        <component v-bind="modal.props" :is="modalName"/>
      </div>
    </transition>
  </div>
</template>

<script>
import * as modals from "@/components/modal/index";
import { mapState, mapMutations } from "vuex";

export default {
  name: "AppModal",
  components: { ...modals },
  computed: {
    ...mapState(["modal", "modalVisible"]),
    modalName() {
      const { name } = this.modal;
      if (!name || !this.$options.components[name]) {
        console.warn(`${name} Modal not found`);
        return null;
      }
      return name;
    }
  },
  methods: {
    ...mapMutations(["toggleModal", "setSize"])
  },
  watch: {
    modalVisible(state) {
      if (state && !document.fullscreenElement) {
        document.documentElement.requestFullscreen();
      }
      if (
        document.fullscreenElement ||
        document.webkitFullscreenElement ||
        document.mozFullScreenElement
      ) {
        document.exitFullscreen();
      }
    }
  },
  async mounted() {
    await this.$nextTick();
    this.setSize({
      width: this.$el.clientWidth,
      height: this.$el.clientHeight
    });

    document.addEventListener("onresize", () => {
      this.setSize({
        width: this.$el.clientWidth,
        height: this.$el.clientHeight
      });
    });
  }
};
</script>

<style lang="scss">
.modal {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 99;
  background-color: #252525;
  display: flex;
  align-content: center;
  align-items: center;
  justify-content: center;

  &__content {
    position: relative;
    width: 100%;
    max-width: $max-width;
    max-height: $max-height;
    height: 100%;
    box-sizing: border-box;
    overflow: hidden;
  }
}
</style>
