<template>
  <label :for="_uid" class="file">
    <input
      type="file"
      :id="_uid"
      ref="fileInput"
      class="file__input"
      v-bind="$attrs"
      @change="changeFileInput"
    >
    <div class="file__preview" v-if="!$slots.default">
      <div v-if="files.length === 0" class="file__view"></div>
      <div class="file__view" v-for="(file, i) in files" :key="i">
        <Icon v-if="file.type.match('video*')" name="Video"/>
        <img v-else :src="URL.createObjectURL(file)" :alt="file.name">
      </div>
    </div>
    <slot/>
  </label>
</template>

<script>
export default {
  name: "File",
  data() {
    return {
      files: []
    };
  },
  computed: {
    URL() {
      return window.URL || window.webkitURL;
    }
  },
  methods: {
    changeFileInput() {
      const files = this.$refs.fileInput.files;
      if (files.length > 0) {
        this.files = this.$refs.fileInput.files;
        this.$emit("change", this.files);
      }
    }
  }
};
</script>

<style lang="scss">
.file {
  position: relative;
  z-index: 99;
  display: flex;
  align-items: center;
  overflow: hidden;

  &__input {
    position: absolute;
    width: 0;
    height: 0;
    opacity: 0;
  }

  &__view {
    width: 30px;
    height: 30px;
    border-radius: 5px;
    border: 1px solid #fff;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
    color: #fff;
    overflow: hidden;

    + .file__view {
      margin-left: 10px;
    }
  }
}
</style>
