<template>
  <div id="storyApp">
    <AppModal/>
    <Stories/>
  </div>
</template>

<script>
import Stories from "@/components/Stories";
import storyData from "@/assets/stories.json";
import { mapMutations } from "vuex";

export default {
  name: "Story",
  props: {
    userData: {
      type: Object,
      default: () => ({
        avatar: "img/user.png",
        title: "My Account",
        userId: 10,
        stories: [
          {
            type: "image",
            mediaUrl: "img/heyy.png",
            background: "",
            viewingUsers: [],
            items: []
          }
        ]
      })
    },
    storyData: {
      type: Array,
      default: () => storyData
    },
    options: {
      type: Object,
      default: () => ({
        duration: 5,
        fonts: {
          standart: {
            label: "Standart",
            font: "300 50px Arial, Helvetica, sans-serif",
            stroke: false
          },
          daktilo: {
            label: "Daktilo",
            font: "500 50px Arial, Helvetica, sans-serif",
            stroke: false
          },
          guclu: {
            label: "Güçlü",
            font: "700 italic 50px Arial, Helvetica, sans-serif",
            stroke: false
          }
        },
        activeFont: "standart",
        colors: ["#fff", "#fbac00", "#fd6532", "#2697fd", "#16fda6"],
        activeColor: 0,
        backgrounds: [
          `linear-gradient(to right bottom,#0e277c,#0c2370,#0a1e64,#081a58,#07164c)`,
          `linear-gradient(to right bottom,#0e277c,#0c2370,#0a1e64,#081a58,#07164c)`
        ],
        activeBackground: 0,
        // more emoji emojipedia.org
        emojis: [
          "0x1F602",
          "0x1F600",
          "0x1F622",
          "0x1F60D",
          "0x1F48E",
          "0x23F0",
          "0x1F579",
          "0x1F4DA"
        ],
        callbacks: {
          sendMessageHandler: message => {
            console.info(message);
          },
          createStoryHandler: (user, story) => {
            console.info(user, story);
          },
          viewingUsers: (story, item, userId) => {
            console.info({
              storyItem: item,
              viewUser: userId
            });
          }
        }
      })
    }
  },
  components: {
    Stories
  },
  methods: {
    ...mapMutations(["setStories", "setOptions", "setUser"])
  },
  watch: {
    storyData(data) {
      this.setStories(data);
    },
    userData(data) {
      this.setUser(data);
    },
    options(newOptions, oldOptions) {
      this.setOptions({ ...oldOptions, ...newOptions });
    }
  },
  mounted() {
    this.setStories(this.storyData);
    this.setUser(this.userData);
    this.setOptions(this.options);
  }
};
</script>

<style lang="scss">
@import "@/assets/scss/_main.scss";
@import "@/assets/scss/_utils.scss";
@import "@/assets/scss/_animation.scss";
</style>
