<template>
  <div class="emoji" v-if="visible">
    <div class="emoji__content">
      <h2 class="emoji__title" v-if="title">{{title}}</h2>
      <transition-group
        tag="div"
        class="emoji__box"
        appear
        enter-active-class="bounceIn"
        leave-active-class="bounceOut"
      >
        <Button
          class="emoji__btn"
          type="emoji"
          v-for="emoji in emojis"
          :key="emoji"
          @click="$emit('selectEmoji', emoji)"
        >{{emoji}}</Button>
      </transition-group>
    </div>
  </div>
</template>

<script>
import { mapMutations, mapGetters } from "vuex";

export default {
  name: "Emoji",
  props: {
    title: {
      type: String
    }
  },
  computed: {
    ...mapGetters({
      visible: "emojiVisible"
    }),
    emojis() {
      if (!this.settings.emojis) return [];
      return this.settings.emojis.map(e => String.fromCodePoint(e));
    }
  },
  methods: {
    ...mapMutations(["toggleEmoji", "closeEmoji"])
  },
  destroyed() {
    this.closeEmoji();
  }
};
</script>

