<template>
  <div class="text-mode" :style="{background: selectedBackground}">
    <TextItem v-model="txt" :color="color" ref="text" @focus="focusText" :font="selectedFont.font"/>
    <Emoji @selectEmoji="selectEmoji"/>
    <EmojiItem v-if="emoji" :emoji="emoji" ref="emoji"/>
    <div class="text-mode__options">
      <ColorSetting v-model="color" v-if="isFocus"/>
      <ActionsBar v-if="!isFocus" @action="action"/>
    </div>
  </div>
</template>

<script>
import { mapMutations } from "vuex";
export default {
  name: "TextMode",
  data() {
    return {
      txt: null,
      emoji: null,
      color: "#fff",
      font: null,
      isFocus: false
    };
  },
  watch: {
    color() {
      this.$refs.text.$el.focus();
    }
  },
  methods: {
    ...mapMutations(["closeEmoji", "updateUser", "toggleModal"]),
    selectEmoji(emoji) {
      this.emoji = emoji;
      this.closeEmoji();
    },
    focusText(status) {
      setTimeout(() => {
        this.isFocus = status;
      }, 100);
    },
    async action() {
      const items = [];

      if (this.txt) {
        const text = this.$refs.text;
        items.push({
          type: "text",
          value: this.txt,
          position: text.coordinat,
          rotate: text.rotation,
          customProperties: { color: this.color, font: this.selectedFont.font }
        });
      }

      if (this.emoji) {
        const emoji = this.$refs.emoji;
        items.push({
          type: "emoji",
          value: this.emoji,
          position: emoji.coordinat,
          rotate: emoji.rotation,
          customProperties: []
        });
      }

      await this.updateUser({
        type: "text",
        mediaUrl: "",
        background: this.selectedBackground,
        viewingUsers: [],
        items,
        duration: this.settings.duration
      });

      this.toggleModal();
    }
  }
};
</script>

<style lang="scss">
.text-mode {
  position: relative;
  height: 100%;
  width: 100%;
  box-sizing: border-box;
  padding-top: 100px;

  &__options {
    position: absolute;
    bottom: 50px;
    left: 0;
    right: 0;
  }
}
</style>