<template>
  <div class="progress">
    <div v-for="index in count" :key="index" :class="variants(index)">
      <span class="progress__wait" :style="activeNumber === index ? {width: `${time}%`}:''"/>
    </div>
  </div>
</template>

<script>
export default {
  name: "Progress",
  props: {
    time: {
      type: Number
    },
    count: {
      type: Number,
      default: 0
    },
    activeNumber: {
      type: Number,
      default: 1
    },
    animation: {
      type: Boolean,
      default: false
    }
  },
  methods: {
    variants(index) {
      return {
        progress__item: true,
        "-active": this.activeNumber === index && this.animation,
        "-end": this.activeNumber > index
      };
    }
  }
};
</script>

<style lang="scss">
.progress {
  display: flex;
  align-items: center;

  &__item {
    height: 2px;
    background-color: rgba(255, 255, 255, 0.35);
    border-radius: 2px;
    width: 100%;
    position: relative;
    overflow: hidden;

    & + & {
      margin-left: 5px;
    }

    &.-end {
      .progress__wait {
        width: 100%;
      }
    }

    &.-active {
      .progress__wait {
        transition: 1s linear;
      }
    }
  }

  &__wait {
    position: absolute;
    left: 0;
    top: 0;
    bottom: 0;
    background-color: #fff;
    width: 0%;
  }
}
</style>
