<template>
  <div class="cube-wrapper">
    <div class="cube" :style="styleCube">
      <div class="cube__box" :style="styleBox">
        <div v-if="$slots.back" class="cube__face -back" :style="styleBack">
          <slot name="back"/>
        </div>
        <div v-if="$slots.top" class="cube__face -top" :style="styleTop">
          <slot name="top"/>
        </div>
        <div v-if="$slots.front" class="cube__face -front" :style="styleFront">
          <slot name="front"/>
        </div>
        <div v-if="$slots.left" class="cube__face -left" :style="styleLeft">
          <slot name="left"/>
        </div>
        <div v-if="$slots.right" class="cube__face -right" :style="styleRight">
          <slot name="right"/>
        </div>
        <div v-if="$slots.bottom" class="cube__face -bottom" :style="styleBottom">
          <slot name="bottom"/>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "Cube3d",
  props: {
    rotateX: {
      type: Number,
      default: 0
    },
    rotateY: {
      type: Number,
      default: 0
    }
  },
  data() {
    return {
      width: 10,
      height: 10,
      perspective: "0",
      perspectiveOrigin: "0 0"
    };
  },
  computed: {
    style() {
      return {
        width: `${this.width}px`,
        height: `${this.height}px`
      };
    },
    styleCube() {
      return {
        ...this.style,
        perspective: this.perspective,
        perspectiveOrigin: this.perspectiveOrigin
      };
    },
    styleBox() {
      return {
        ...this.style,
        transform: `rotateY(${this.rotateY}deg) rotateX(${this.rotateX}deg)`
      };
    },
    styleBack() {
      return {
        ...this.style,
        transform: `rotateY(180deg) translateZ(${this.width / 2}px)`
      };
    },
    styleFront() {
      return {
        ...this.style,
        transform: `rotateY(0deg) translateZ(${this.width / 2}px)`
      };
    },
    styleTop() {
      return {
        height: this.style.width,
        width: this.style.width,
        transform: `rotateX(-90deg) translateZ(${-this.width / 2}px)`
      };
    },
    styleBottom() {
      return {
        height: this.style.width,
        width: this.style.width,
        transform: `rotateX(90deg) translateZ(${-this.width / 2}px)`
      };
    },
    styleLeft() {
      return {
        ...this.style,
        transform: `rotateY(-90deg) translateZ(${this.width / 2}px)`
      };
    },
    styleRight() {
      return {
        ...this.style,
        transform: `rotateY(90deg) translateZ(${this.width / 2}px)`
      };
    }
  },
  async mounted() {
    await this.$nextTick();
    this.width = this.$el.clientWidth;
    this.height = this.$el.clientHeight;

    const listeners = [
      "onresize",
      "fullscreenchange",
      "webkitfullscreenchange",
      "mozfullscreenchange"
    ];
    listeners.forEach(listener => {
      document.addEventListener(listener, async () => {
        await this.$nextTick();
        this.width = this.$el.clientWidth;
        this.height = this.$el.clientHeight;
      });
    });
  }
};
</script>

<style lang="scss">
.cube-wrapper {
  width: 100%;
  height: 100%;
  position: relative;
}

.cube {
  perspective-origin: 0 0;
  perspective: 0;
  font-size: 30px;
  color: #fff;
  text-align: center;
  position: relative;

  &__box {
    position: absolute;
    transform-style: preserve-3d;
    backface-visibility: hidden;
    transition: transform 350ms ease-in-out, -webkit-transform 350ms ease-in-out;
  }

  &__face {
    position: absolute;
    background-color: rgb(24, 24, 24);
    overflow: hidden;
    backface-visibility: hidden;
    will-change: transform;

    &.-back {
      // background-color: grey;
    }

    &.-top {
      // background-color: red;
    }

    &.-front {
      // background-color: pink;
    }

    &.-left {
      // background-color: black;
    }

    &.-right {
      // background-color: yellow;
    }

    &.-bottom {
      // background-color: blue;
    }
  }
}
</style>