<template>
  <div class="options-bar">
    <Button :class="optionIndex === 0 ? '-active' : ''" @click="setOption(0)">
      <Icon name="SmileEmoji"/>
    </Button>
    <Button type="circle" :class="optionIndex === 1 ? '-active' : ''" @click="setOption(1)">
      <Icon name="Edit"/>
    </Button>
    <div class="options-bar__center">
      <Button
        v-if="optionIndex === 1"
        class="-position-center"
        type="radius"
        :style="{ font: selectedFont.font }"
        @click.prevent="changeFontStyle"
      >{{ selectedFont.label }}</Button>
    </div>
    <Close/>
  </div>
</template>

<script>
import { mapMutations, mapGetters } from "vuex";

export default {
  name: "OptionsBar",
  methods: {
    ...mapMutations(["changeTypeOption", "toggleEmoji", "closeEmoji"]),
    setOption(index) {
      if (index === this.optionIndex) {
        this.changeTypeOption(-1);
      } else {
        if (index === 0) {
          this.toggleEmoji();
        } else {
          this.closeEmoji();
        }
        this.changeTypeOption(index);
      }
    }
  },
  computed: {
    ...mapGetters({
      optionIndex: "activeOption"
    })
  }
};
</script>

<style lang="scss">
.options-bar {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  min-height: 40px;

  &__center {
    flex: 1;
    text-align: center;
  }
}
</style>
