<template>
  <div class="circle-box" :class="variants">
    <a class="circle-box__close" v-if="closed" href="#" @click.prevent="closeClicked">
      <Icon name="Close" fill="#000"/>
    </a>
    <div class="circle-box__content" v-on="$listeners" :style="{ width: size, height: size }">
      <img v-if="!$slots.default && avatar" class="circle-box__avatar" :src="avatar">
      <slot/>
    </div>
    <p class="circle-box__title" v-if="title">{{ title }}</p>
  </div>
</template>

<script>
export default {
  name: "CircleBox",
  props: {
    click: {
      type: Function
    },
    closed: {
      type: Boolean,
      default: true
    },
    avatar: {
      type: String
    },
    title: {
      type: String,
      default: "Title"
    },
    size: {
      type: String,
      default: "80px"
    },
    visible: {
      type: Boolean,
      default: false
    },
    type: {
      type: String
    }
  },
  computed: {
    variants() {
      return {
        "-visible": this.visible,
        [`-${this.type}`]: !!this.type
      };
    }
  },
  methods: {
    closeClicked() {
      this.$emit("close");
    }
  }
};
</script>

<style lang="scss">
.circle-box {
  position: relative;
  display: inline-block;
  text-align: center;
  transition: all 1s ease-in-out;

  &__close {
    position: absolute;
    top: -4px;
    right: -4px;
  }

  &__content {
    padding: 2px;
    border-radius: 100px;
    background-color: #fff;
    border: 2px solid #cd486b;
    overflow: hidden;
    display: flex;
    align-items: center;
    justify-content: center;
  }

  &__avatar {
    width: 100%;
    height: 100%;
    border: none;
    border-radius: 100px;
  }

  &__title {
    margin-top: 8px;
    font-size: 13px;
    color: rgb(46, 53, 57);
  }

  &.-visible {
    .circle-box__content {
      border-color: #ccc;
    }
  }

  &.-action {
    .circle-box__content {
      background-color: #fff;
      color: #fff;
      width: 80px;
      height: 80px;
      position: relative;
      z-index: 1;
      border-color: transparent;

      &:active {
        &:before {
          box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.5);
        }
      }

      &:before {
        content: "";
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%);
        width: 70px;
        height: 70px;
        border-radius: 35px;
        background-color: $blue;
        z-index: -1;
        box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.5);
      }
    }
  }
}
</style>
