<template>
  <Button
    v-if="emojiVisible"
    type="radius"
    size="sm"
    @click="close"
    v-bind="$attrs"
    v-on="$listeners"
  >Tamam</Button>
  <Button v-else @click="close" v-bind="$attrs" v-on="$listeners">
    <Icon name="Close" fill="#fff" width="30" height="30"/>
  </Button>
</template>

<script>
import { mapMutations, mapGetters } from "vuex";
export default {
  name: "Close",
  computed: {
    ...mapGetters(["emojiVisible", "modalVisible"])
  },
  methods: {
    ...mapMutations(["toggleModal", "closeEmoji"]),
    close() {
      if (!this.emojiVisible) {
        this.toggleModal();
      } else {
        setTimeout(() => {
          this.closeEmoji();
        }, 200);
      }
    }
  }
};
</script>

