<template>
  <transition-group
    tag="div"
    class="color-setting"
    appear
    enter-active-class="bounceIn"
    leave-active-class="bounceOut"
  >
    <label
      v-for="(color, i) in settings.colors"
      :key="color"
      :for="`c-${_uid + i}`"
      class="color-setting__btn"
    >
      <Button type="color" :style="{ backgroundColor: color }">
        <input
          type="radio"
          :id="`c-${_uid + i}`"
          :name="`color-${_uid}`"
          :value="color"
          v-model="val"
        >
        <Icon v-if="val === color" name="Paint" fill="gray"/>
      </Button>
    </label>
  </transition-group>
</template>

<script>
export default {
  name: "ColorSettings",
  props: {
    value: {
      type: String,
      default: "#fff"
    }
  },
  computed: {
    val: {
      get() {
        return this.value;
      },
      set(value) {
        this.$emit("input", value);
      }
    }
  }
};
</script>

<style lang="scss">
.color-setting {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;

  input {
    position: absolute;
    opacity: 0;
  }

  &__btn {
    display: inline-block;
    cursor: pointer;

    & + & {
      margin-left: 5px;
    }
  }
}
</style>