<template>
  <div class="emoji-create" v-hammer:pan.all="onPan" v-hammer:rotate="onRotate">{{emoji}}</div>
</template>

<script>
export default {
  name: "EmojiCreate",
  props: {
    emoji: {
      type: String
    },
    editing: {
      type: Boolean,
      default: true
    },
    position: {
      type: Object,
      default: () => ({})
    },
    rotate: {
      type: Number,
      default: 0
    }
  },
  data() {
    return {
      lastX: 0,
      lastY: 0,
      isDragging: false,
      coordinat: this.position,
      rotation: this.rotate
    };
  },
  methods: {
    onRotate(ev) {
      if (!this.editing) return false;
      this.readonly = true;
      const elem = ev.target;
      elem.style.transform = `rotate(${ev.rotation}deg)`;
      this.rotation = ev.rotation;
      this.readonly = false;
    },
    onPan(ev) {
      if (!this.editing) return false;
      const elem = ev.target;

      if (!this.isDragging) {
        this.isDragging = true;
        this.lastX = this.coordinat.x || elem.offsetLeft;
        this.lastY = this.coordinat.y || elem.offsetTop;
        elem.style.transform = "none";
      }

      const posX = ev.deltaX + this.lastX;
      const posY = ev.deltaY + this.lastY;
      elem.style.left = `${posX}px`;
      elem.style.top = `${posY}px`;

      this.coordinat = {
        x: posX,
        y: posY
      };
    }
  },
  async mounted() {
    await this.$nextTick();
    if (this.coordinat && (this.coordinat.x || this.coordinat.y)) {
      if (this.coordinat.x) {
        this.$el.style.left = `${this.coordinat.x}px`;
      }
      if (this.coordinat.y) {
        this.$el.style.top = `${this.coordinat.y}px`;
      }
      this.$el.style.transform = "none";
    }
    if (this.rotation) {
      this.$el.style.transform = `rotate(${this.rotation}deg)`;
    }
  }
};
</script>

<style lang="scss">
.emoji-create {
  position: absolute;
  left: 0;
  top: 0;
  cursor: move;
  z-index: 999;
  font-size: 40px;
  width: 50px;
  height: 50px;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
</style>
