<template>
  <div class="normal-mode">
    <Media class="normal-mode__media" :src="file.src" ref="media" :type="file.type"/>
    <TextItem
      v-model="txt"
      :color="color"
      :bg="higlight"
      ref="text"
      :position="textPosition"
      :font="selectedFont.font"
      @focus="focusText"
    />
    <Emoji @selectEmoji="selectEmoji"/>
    <EmojiItem v-if="emoji" :emoji="emoji" ref="emoji"/>
    <div class="normal-mode__footer">
      <ColorSetting v-model="color" v-if="isFocus"/>
      <ActionsBar v-if="!isFocus" @action="action">
        <template slot="left">
          <File multiple accept="file_extension|video/*|image/*" @change="fileChangeHandler"/>
        </template>
      </ActionsBar>
    </div>
  </div>
</template>

<script>
import { mapMutations } from "vuex";

export default {
  name: "NormalMode",
  data() {
    return {
      file: {
        src: "/img/story.png",
        type: "image"
      },
      txt: null,
      emoji: null,
      color: "#fff",
      font: null,
      isFocus: false,
      higlight: "#0e277c",
      textPosition: {
        y: 100
      }
    };
  },
  methods: {
    ...mapMutations(["closeEmoji", "updateUser", "toggleModal"]),
    selectEmoji(emoji) {
      this.emoji = emoji;
      this.closeEmoji();
    },
    focusText(status) {
      setTimeout(() => {
        this.isFocus = status;
      }, 100);
    },
    fileChangeHandler(files) {
      const file = files[0];
      if (file.type.match("image.*|video.*")) {
        const reader = new FileReader();
        reader.readAsDataURL(file);
        reader.onload = evt => {
          if (evt.target.readyState == FileReader.DONE) {
            this.file = {
              src: evt.target.result,
              type: file.type.match("image.*") ? "image" : "video"
            };
          }
        };
      }
    },
    async action() {
      const items = [];

      if (this.txt) {
        const text = this.$refs.text;
        items.push({
          type: "text",
          value: this.txt,
          position: text.coordinat,
          rotate: text.rotation,
          customProperties: {
            color: this.color,
            font: this.selectedFont.font,
            bg: this.higlight
          }
        });
      }

      if (this.emoji) {
        const emoji = this.$refs.emoji;
        items.push({
          type: "emoji",
          value: this.emoji,
          position: emoji.coordinat,
          rotate: emoji.rotation,
          customProperties: []
        });
      }

      const media = this.$refs.media;
      await this.updateUser({
        type: this.file.type,
        mediaUrl: this.file.src,
        background: null,
        viewingUsers: [],
        items,
        duration: media.duration ? media.duration : this.settings.duration
      });

      this.toggleModal();
    }
  }
};
</script>

<style lang="scss">
.normal-mode {
  position: relative;
  height: 100%;
  width: 100%;
  box-sizing: border-box;
  padding-top: 100px;

  &__media {
    z-index: 0 !important;
  }

  &__footer {
    position: absolute;
    bottom: 50px;
    left: 0;
    right: 0;
  }
}
</style>