import Vue from "vue";
import Vuex from "vuex";
import guid from "@/utils/guid";

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    types: ["Yazı", "Normal"],
    typeOptions: ["Emoji", "Text"],
    activeType: 0,
    activeOption: 1,
    activeFile: null,
    modal: {
      name: null,
      props: {}
    },
    modalVisible: false,
    emojiVisible: false,
    user: {
      stories: []
    },
    size: {
      width: 0,
      height: 0
    },
    stories: [],
    activeStory: null,
    activeStoryItemIndex: 0,
    options: {}
  },
  getters: {
    types: state => state.types,
    typeOptions: state => state.typeOptions,
    size: state => state.size,
    activeType: state => state.activeType,
    activeOption: state => state.activeOption,
    stories: state => state.stories,
    options: state => state.options,
    user: state => state.user,
    emojiVisible: state => state.emojiVisible,
    modalVisible: state => state.modalVisible,
    activeFile: state => state.activeFile,
    activeStoryIndex: state => {
      if (!state.activeStory) return 0;
      return state.stories.findIndex(s => s.id === state.activeStory.id);
    },
    prevStories(state, getters) {
      return state.stories.filter(
        (_, index) => index < getters.activeStoryItemIndex
      );
    },
    nextStories(state, getters) {
      return state.stories.filter(
        (_, index) => index > getters.activeStoryItemIndex
      );
    },
    activeStory: state => {
      return state.activeStory;
    },
    prevStory: (state, getters) => {
      if (getters.activeStoryIndex === 0) return null;
      return state.stories[getters.activeStoryIndex - 1];
    },
    nextStory: (state, getters) => {
      if (getters.activeStoryIndex >= state.stories.length) return null;
      return state.stories[getters.activeStoryIndex + 1];
    },
    activeStoryItemIndex: state => state.activeStoryItemIndex,
    activeStoryItems: state => {
      if (!state.activeStory) return null;
      return state.activeStory.stories.map(s => {
        return {
          id: guid(),
          ...s
        };
      });
    },
    activeItem(state, getters) {
      return getters.activeStoryItems[getters.activeStoryItemIndex];
    }
  },
  mutations: {
    changeType(state, typeIndex) {
      state.activeType = typeIndex;
    },
    changeTypeOption(state, optionIndex) {
      state.activeOption = optionIndex;
    },
    changeModal(state, payload) {
      state.emojiVisible = false;
      state.modalVisible = false;
      state.modal = payload;
      state.modalVisible = true;
    },
    toggleModal: (state, payload) => {
      state.modalVisible = !state.modalVisible;
      if (typeof payload === "function") {
        payload();
      }
    },
    toggleEmoji: state => (state.emojiVisible = !state.emojiVisible),
    closeEmoji: state => (state.emojiVisible = false),
    storyItemDelete(state, id) {
      const findIndex = state.stories.findIndex(s => s.id === id);
      state.user.stories.splice(findIndex, 1);
    },
    setOptions(state, payload) {
      state.options = payload;
    },
    setStories(state, payload) {
      state.stories = payload.map(s => {
        return {
          id: guid(),
          ...s
        };
      });
    },
    setSize(state, payload) {
      state.size = payload;
    },
    setUser(state, payload) {
      state.user = { id: guid(), ...payload };
    },
    async updateUser(state, payload) {
      if (
        state.options.callbacks.createStoryHandler &&
        typeof state.options.callbacks.createStoryHandler === "function"
      ) {
        state.user.stories = [...state.user.stories, payload];
        return await state.options.callbacks.createStoryHandler(
          state.user,
          payload
        );
      }
    },
    async sendMessage(state, payload) {
      if (
        state.options.callbacks.sendMessageHandler &&
        typeof state.options.callbacks.sendMessageHandler === "function"
      ) {
        return await state.options.callbacks.sendMessageHandler(payload);
      }
    },
    removeStory(state, storyId) {
      const storyIndex = state.stories.findIndex(s => s.id === storyId);
      state.stories.splice(storyIndex, 1);
    },
    closeStory(state) {
      state.activeStory = null;
      state.modalVisible = false;
      state.emojiVisible = false;
    },
    setActiveStory(state, storyId) {
      const story = state.stories.find(s => s.id === storyId);
      state.activeStoryItemIndex = 0;
      state.activeStory = story;
    },
    setActiveStoryByIndex(state, index) {
      const story = state.stories[index];
      if (story) {
        state.activeStory = story;
      }
    },
    setActiveStoryItemIndex(state, index) {
      state.activeStoryItemIndex = index;
    },
    async setViewUser(state, payload) {
      const story = state.stories.find(s => s.id === payload.id);
      if (story) {
        const viewUsers = story.stories[payload.index].viewingUsers;
        if (viewUsers) {
          const uID = viewUsers.findIndex(_ => _ === state.user.id);
          if (uID < 0) {
            story.stories[payload.index].viewingUsers.push(state.user.id);
            if (
              state.options.callbacks.viewingUsers &&
              typeof state.options.callbacks.viewingUsers === "function"
            ) {
              return await state.options.callbacks.viewingUsers(
                story,
                story.stories[payload.index],
                state.user.id
              );
            }
          }
        }
      }
    },
    goNextStory(state) {
      const nextStoryIndex = this.getters.activeStoryIndex + 1;
      if (nextStoryIndex > state.stories.length - 1) {
        this.commit("closeStory");
      } else {
        state.activeStoryItemIndex = 0;
        state.activeStory = state.stories[nextStoryIndex];
      }
    },
    goPrevStory(state) {
      const prevStoryIndex = this.getters.activeStoryIndex - 1;
      if (prevStoryIndex < 0) {
        this.commit("closeStory");
      } else {
        state.activeStoryItemIndex = 0;
        state.activeStory = state.stories[prevStoryIndex];
      }
    }
  }
});
