import Vue from "vue";
import { mapState } from "vuex";

Vue.mixin({
  computed: {
    ...mapState({
      settings: "options"
    }),
    selectedFont() {
      return this.settings.fonts[this.settings.activeFont];
    },
    selectedBackground() {
      return this.settings.backgrounds[this.settings.activeBackground];
    },
    selectedColor() {
      return this.settings.colors[this.settings.activeColor];
    }
  },
  methods: {
    changeColor(index) {
      this.settings.activeColor = index;
    },
    changeBackground() {
      if (
        this.settings.backgrounds.length - 1 ===
        this.settings.activeBackground
      ) {
        this.settings.activeBackground = 0;
      } else {
        this.settings.activeBackground += 1;
      }
    },
    changeFontStyle() {
      const fontKeys = Object.keys(this.settings.fonts);
      const activeKeyIndex = fontKeys.findIndex(
        f => this.settings.fonts[f].label === this.selectedFont.label
      );
      if (activeKeyIndex + 1 === fontKeys.length) {
        this.settings.activeFont = fontKeys[0];
      } else {
        this.settings.activeFont = fontKeys[activeKeyIndex + 1];
      }
    }
  }
});
