<template>
  <img v-if="type === 'image'" class="media" :src="src" @load="loadMedia">
  <video
    v-else-if="type === 'video'"
    class="media"
    :src="src"
    @loadstart="loadMedia"
    @loadedmetadata="loadMetaData"
    ref="video"
    autoplay
    playsinline
    webkit-playsinline
  />
</template>

<script>
import guid from "@/utils/guid";

export default {
  name: "Media",
  props: {
    type: {
      type: String,
      required: true
    },
    src: {
      type: String,
      required: true
    }
  },
  data() {
    return {
      duration: 0
    };
  },
  computed: {
    srcGuid() {
      return `${this.src}?guid=${guid()}`;
    }
  },
  methods: {
    loadMetaData() {
      if (this.$refs.video) {
        this.duration = this.$refs.video.duration;
      }
    },
    loadMedia() {
      this.$emit("load", true);
    }
  }
};
</script>

<style lang="scss" scoped>
.media {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
  object-fit: cover;
  pointer-events: none;
}
</style>