<template>
  <div class="tab">
    <div class="tab__header" v-if="$slots.header">
      <slot name="header"/>
    </div>
    <div class="tab__content">
      <slot/>
    </div>
    <div class="tab__footer">
      <slot name="footer" v-if="$slots.footer"/>
      <nav v-hammer:swipe.all="onSwipe" class="tab__nav" :style="navStyles">
        <Button
          v-for="(type, i) in tabs"
          :key="i"
          :class="i === activeTabIndex ? 'tab__btn -active' : 'tab__btn'"
          ref="tabBtn"
          @click.prevent="activeTabIndex = i"
        >{{ type }}</Button>
      </nav>
    </div>
  </div>
</template>

<script>
export default {
  name: "Tab",
  props: {
    tabs: {
      type: Array
    }
  },
  data() {
    return {
      activeTabIndex: 0,
      tabNavPos: 0
    };
  },
  computed: {
    navStyles() {
      return {
        transform: `translateX(${this.tabNavPos}px)`
      };
    }
  },
  watch: {
    activeTabIndex(index) {
      this.setTabNavPosition();
      this.$emit("changeTab", index);
    }
  },
  methods: {
    setTabNavPosition() {
      const currentNav = this.$refs.tabBtn[this.activeTabIndex];
      if (!currentNav) return;
      const $currentNav = currentNav.$el;
      const centerPos = this.$el.clientWidth / 2;
      const navWidth = $currentNav.clientWidth;
      this.tabNavPos = centerPos - $currentNav.offsetLeft - navWidth / 2;
    },
    onSwipe(e) {
      switch (e.type) {
        case "swipeleft":
          this.activeTabIndex += 1;
          break;
        case "swiperight":
          this.activeTabIndex -= 1;
          break;
      }
    }
  },
  async mounted() {
    await this.$nextTick();
    this.setTabNavPosition();
  }
};
</script>

