<template>
  <section class="stories">
    <CircleBox
      type="action"
      :closed="false"
      title="Hikaye Ekle"
      class="-m-right-15"
      @click="createStory"
    >
      <Icon name="Plus"/>
    </CircleBox>
    <CircleBox
      v-if="user.stories.length > 0"
      class="stories__item bounceIn -m-right-15"
      @click="openMyStory"
      title="My Story"
      :avatar="user.avatar"
      :closed="false"
    />
    <transition-group
      class="stories__wrapper"
      tag="div"
      enter-active-class="bounceIn"
      leave-active-class="bounceOut"
    >
      <CircleBox
        class="stories__item bounceIn"
        v-for="story in stories"
        :key="story.id"
        @close="removeStory(story.id)"
        @click="openStory(story.id)"
        :avatar="story.avatar"
        :title="story.title"
      />
    </transition-group>
  </section>
</template>

<script>
import { mapMutations, mapGetters } from "vuex";

export default {
  name: "Stories",
  props: {
    value: {
      type: Array,
      default: () => []
    }
  },
  computed: {
    ...mapGetters(["stories", "user"])
  },
  methods: {
    ...mapMutations(["removeStory", "setActiveStory", "changeModal"]),

    createStory() {
      this.changeModal({
        name: "StoryCreate"
      });
    },
    openStory(id) {
      this.setActiveStory(id);
      this.changeModal({
        name: "Story"
      });
    },
    openMyStory() {
      this.changeModal({
        name: "Profile"
      });
    }
  }
};
</script>

<style lang="scss">
.stories {
  display: flex;
  align-items: center;
  justify-items: center;
  overflow: auto;
  white-space: nowrap;
  padding: 20px 0;

  &__item {
    + .stories__item {
      margin-left: 15px;
    }
  }
}
</style>
