<template>
  <div
    class="story"
    @click="onClick"
    :style="{background: (activeItem && activeItem.background) ? activeItem.background : null }"
  >
    <header class="story__header" v-show="!full">
      <Progress
        :count="storyItems.length"
        :time="progressTime"
        :animation="!wait"
        :activeNumber="activeProgressIndex"
      />
      <div class="story__info">
        <CircleImg :src="storyData.avatar" :title="storyData.title"/>
        <span class="story__title">{{ storyData.title }}</span>
      </div>
    </header>
    <Emoji title="Hızlı Cevap" @selectEmoji="sendEmoji"/>
    <Media
      v-if="activeItem && activeItem.type !== 'text'"
      :type="activeItem.type"
      :src="activeItem.mediaUrl"
      @load="loadMedia"
    />
    <div class="story__items" v-if="activeItem && activeItem.items">
      <component
        v-for="(item, key) in activeItem.items"
        :key="key"
        :is="item.type | componentType"
        :emoji="item.value"
        :editing="false"
        :value="item.value"
        :color="item.customProperties.color"
        :bg="item.customProperties.bg"
        :font="item.customProperties.font"
        :rotate="item.rotate"
        :position="item.position"
      />
    </div>
    <footer class="story__footer" v-show="!full" v-if="!$slots.footer">
      <Button type="circle" size="md" class="-m-right-10">
        <File accept="image/*" @change="replyPhoto">
          <Icon name="Camera"/>
        </File>
      </Button>
      <InputText
        placeholder="Mesaj Gönder"
        v-model="message"
        @keyup.enter="messageHandler"
        @focus="() => focus = true"
        @blur="() => focus = false"
      >
        <Button>
          <Icon name="More"/>
        </Button>
      </InputText>
    </footer>
    <footer class="story__footer" v-show="!full" v-else>
      <slot name="footer"/>
    </footer>
  </div>
</template>

<script>
import { mapMutations, mapGetters } from "vuex";

export default {
  name: "Story",
  props: {
    storyData: {
      type: Object,
      required: true
    },
    pause: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      activeProgressIndex: 1,
      currentTime: 0,
      message: "",
      focus: false,
      mediaLoaded: false,
      wait: false,
      storyLoop: null
    };
  },
  computed: {
    ...mapGetters({
      activeStory: "activeStory",
      activeStoryIndex: "activeStoryIndex",
      activeStoryItemIndex: "activeStoryItemIndex",
      activeUser: "user",
      emojiVisible: "emojiVisible"
    }),
    progressTime() {
      if (!this.activeItem || this.currentTime === 0) return 0;
      const time = this.activeItem.duration || 10;
      return (this.currentTime / time) * 100;
    },
    stop() {
      return (
        !this.mediaLoaded ||
        this.focus ||
        this.emojiVisible ||
        this.pause ||
        this.wait
      );
    },
    full() {
      return this.pause && (!this.focus || !this.emojiVisible);
    },
    storyItems() {
      return this.storyData.stories;
    },
    activeItem() {
      return this.storyItems[this.activeProgressIndex - 1];
    }
  },
  watch: {
    "storyData.id"() {
      this.storyLoop = null;
      this.wait = true;
      this.currentTime = 0;
      this.activeProgressIndex = 1;
      setTimeout(() => {
        this.timer();
      }, this.activeItem.duration);
    },
    stop(status) {
      if (!status) {
        this.timer();
      }
    },
    focus() {
      if (this.message.length === 0) {
        this.toggleEmoji();
      }
    },
    message() {
      if (this.message.length > 0) {
        this.closeEmoji();
      } else {
        this.toggleEmoji();
      }
    },
    activeProgressIndex(index) {
      if (index > this.storyItems.length) {
        this.goNextStory();
      }
      if (index < 1) {
        this.goPrevStory();
      }
    }
  },
  methods: {
    ...mapMutations([
      "changeModal",
      "setActiveStoryItemIndex",
      "toggleEmoji",
      "closeEmoji",
      "sendMessage",
      "setViewUser",
      "goNextStory",
      "goPrevStory"
    ]),
    onClick(e) {
      if (this.storyLoop) {
        clearInterval(this.storyLoop);
        this.storyLoop = null;
      }
      this.wait = true;
      this.currentTime = 0;
      const screenWidth = this.$el.clientWidth / 2;
      if (e.x >= screenWidth) {
        if (this.activeProgressIndex < this.storyItems.length) {
          this.activeProgressIndex += 1;
          setTimeout(() => {
            this.timer();
          }, this.activeItem.duration);
        } else {
          this.goNextStory();
        }
      } else {
        if (this.activeProgressIndex > 1) {
          this.activeProgressIndex -= 1;
          setTimeout(() => {
            this.timer();
          }, this.activeItem.duration);
        } else {
          this.goPrevStory();
        }
      }
    },
    loadMedia() {
      this.mediaLoaded = true;
    },
    timer() {
      this.wait = false;
      const progress = () => {
        if (this.progressTime >= 100) {
          clearInterval(this.storyLoop);
          if (this.activeProgressIndex < this.storyItems.length) {
            this.mediaLoaded = false;
            this.activeProgressIndex += 1;
            this.currentTime = 0;
            this.timer();
            this.viewUser();
          }
        }
        if (this.stop) {
          clearInterval(this.storyLoop);
        } else {
          this.currentTime += 1;
        }
      };
      this.storyLoop = setInterval(progress, 1000);
    },
    reset() {},
    viewUser() {
      this.setViewUser({
        id: this.storyData.id,
        index: this.activeProgressIndex - 1
      });
    },
    sendEmoji(emoji) {
      this.sendMessage({
        message: emoji,
        story: this.storyData,
        storyMedia: this.storyItems[this.activeProgressIndex - 1],
        sender: this.activeUser
      });
      this.toggleEmoji();
    },
    messageHandler() {
      this.sendMessage({
        message: this.message,
        story: this.storyData,
        storyMedia: this.storyItems[this.activeProgressIndex - 1],
        sender: this.activeUser
      });
    },
    replyPhoto(files) {
      const file = files[0];
      this.changeModal({
        name: "Reply",
        props: {
          media: file,
          story: this.storyData
        }
      });
    }
  }
};
</script>


