<template>
  <p
    class="canvas-text"
    :contenteditable="editing"
    v-bind="$attrs"
    v-hammer:pan.all="onPan"
    v-hammer:rotate="onRotate"
    @focus="onFocus(true)"
    @blur="onFocus(false)"
    @input="onInput"
    ref="el"
    :style="style"
  >
    <mark :style="{background: bg}">{{value || placeholder}}</mark>
  </p>
</template>

<script>
import { mapGetters } from "vuex";
export default {
  name: "TextItem",
  props: {
    value: {},
    font: {
      type: String
    },
    color: {
      type: String
    },
    position: {
      type: Object,
      default: () => ({})
    },
    rotate: {
      type: Number,
      default: 0
    },
    editing: {
      type: Boolean,
      default: true
    },
    bg: {
      type: String,
      default: "transparent"
    },
    placeholder: {
      type: String,
      default: "Yazmak için tıkla"
    },
    selectedText: {
      type: String,
      default: "Bir şeyler yazın"
    },
    vertical: {
      type: String,
      default: "top"
    }
  },
  data() {
    return {
      lastX: 0,
      lastY: 0,
      isDragging: false,
      coordinat: this.position,
      rotation: this.rotate,
      readonly: false,
      focus: false
    };
  },
  computed: {
    ...mapGetters(["size"]),
    style() {
      return {
        font: this.font,
        color: this.color
      };
    }
  },
  methods: {
    onInput(e) {
      const value = e.target.innerText;
      this.$emit("input", value);
    },
    onFocus(status) {
      if (!this.readonly || !this.editing) {
        this.focus = status;
        this.$emit("focus", status);
      }
    },
    onRotate(ev) {
      if (!this.editing || this.readonly) return false;
      this.readonly = true;
      const elem = this.$refs.el;
      elem.style.transform = `rotate(${ev.rotation}deg)`;
      this.rotation = ev.rotation;
      this.readonly = false;
    },
    onPan(ev) {
      if (!this.editing || this.readonly) return false;
      this.readonly = true;
      const elem = this.$refs.el;

      if (!this.isDragging) {
        this.isDragging = true;
        this.lastX = this.coordinat.x || elem.offsetLeft;
        this.lastY = this.coordinat.y || elem.offsetTop;
        elem.style.transform = "none";
      }

      const posX = ev.deltaX + this.lastX;
      const posY = ev.deltaY + this.lastY;
      elem.style.left = `${posX}px`;
      elem.style.top = `${posY}px`;

      this.coordinat = {
        x: posX,
        y: posY
      };

      this.readonly = false;
    }
  },
  async mounted() {
    await this.$nextTick();
    if (this.coordinat) {
      if (this.coordinat.x) {
        this.$refs.el.style.left = `${this.coordinat.x}px`;
      }
      if (this.coordinat.y) {
        this.$refs.el.style.top = `${this.coordinat.y}px`;
      }
    }

    if (this.rotation) {
      this.$refs.el.style.transform = `rotate(${this.rotation}deg)`;
    }
  }
};
</script>

<style lang="scss">
.canvas-text {
  border: none;
  background: transparent;
  color: #fff;
  cursor: move;
  font-size: 30px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 1;

  mark {
    color: currentColor;
  }

  &:focus,
  &:active {
    outline: none;
    box-shadow: none;
    -webkit-box-shadow: none;
    box-shadow: none;
  }

  &::-moz-focus-inner {
    border: 0;
  }

  &::placeholder {
    color: currentColor;
    opacity: 1;
  }

  &:-ms-input-placeholder {
    color: currentColor;
  }

  &::-ms-input-placeholder {
    color: currentColor;
  }
}
</style>

